const GridWidget: React.FC<{ title: string, listData: { text: string, url?: string, router?: string }[] }> = ({
  title,
  listData
}) => {

   const scrollToFn = useCallback(
     (id:string) => {
      scroller.scrollTo(id, {
        duration: 500,
        delay: 100,
        smooth: 'easeInOutQuint',
    })
     },
     [],
   )
  return (
    <GridStyle item md={3} sm={4} xs={6}>
      <p className={'title'}>{title}</p>
      {
        listData.map((ev) => {
          if (ev.url)
            return <a key={ev.text} href={ev.url}>{ev.text}</a>;
          else
            return <a key={ev.text} onClick={()=>{
              scrollToFn(ev.router || '')
            }}>{ev.text}</a>;
        }
        )
      }
    </GridStyle>
  );
}

