export const Home5Widget: React.FC = () => {
  const [expanded, setExpanded] = React.useState<any>(listFq[0].text);

  const handleChange = (panel: string) => (event: React.ChangeEvent<{}>, newExpanded: boolean) => {
    setExpanded(newExpanded ? panel : false);
  };

  return (
    <WapStyle>

      <h2 className={'title'}>Frequently Asked Questions</h2>
      <div className='box'>
        {listFq.map((ev)=><CellWidget
          handleChange={handleChange}
          expanded={expanded}
          panel={ev.title}
          title={ev.title}
          sub={ev.text} />)}
      </div>


    </WapStyle>
  );
}



